    import uniStatusBar from "../../components/uni-status-bar/uni-status-bar.vue"
	import otherLogin from "../../components/home/other-login.vue";
    export default {
        components:{
            uniStatusBar,
            otherLogin
        },
		data() {
			return {
                vericodeAble:true,
                isLoginById:true,
				disable:true ,//是否禁用按钮
				loading:false,
                userName:"",
                pw:"",
                phoneNum:"",
                vericode:"",
                countDown:0
			}
		},
		methods: {
            //初始化表单
            initInput(){
                this.userName = "";
                this.pw = "";
                this.phoneNum = "";
                this.vericode = "";
            },
			back(){
                uni.navigateBack()
            },
            submit(){
                //账号密码登录
                if(this.isLoginById){
                    console.log("login: id:"+this.userName+" pw:"+this.pw)
                }
                else{
                    console.log("login: phonenum:"+this.phoneNum+" verify code:"+this.vericode)
                }
            },
            // 切换登陆方式
            loginSwitch(){
                this.initInput();
                this.isLoginById = !this.isLoginById
            },
            // 改变按钮状态
            OnBtnChange(){
                if((this.userName && this.pw) || (this.phoneNum && this.vericode 
                && this.isPhoneNumVaild(this.phoneNum)) 
               ){
                    this.disable = false
                }
                else{
                    this.disable = true
                }
            },
            //手机号码合法性
            isPhoneNumVaild(phoneNum){
                //手机号正则
                let mPattern = /^1[34578]\d{9}$/; 
                if(mPattern.test(this.phoneNum)){
                    return true;
                }
                else{
                    return false;
                }
            },
            //获取验证码
            getVericode(){
                this.vericodeAble = false
                if(this.countDown > 0){
                    return ;//禁用
                }
                else{                  
                    //输出 true
                    if(!this.isPhoneNumVaild(this.phoneNum)){
                        uni.showToast({
                            title:"请检查手机号码格式！",
                            icon:"none"
                        });
                        this.vericodeAble = true
                        return
                    }
                    //TODO 请求服务器，发送验证码到手机
                    //发送成功 开始倒计时
                    this.countDown = 5;
                    let timer = setInterval(()=>{
                         this.countDown--;
                         if(this.countDown == 0){
                            this.vericodeAble = true
                            clearInterval(timer);
                            this.countDown = 0;
                            return;
                         }
                         else{
                              this.vericodeAble = false
                         }
                    },1000)
                }
            }
		},
        watch:{
            userName(val){
                this.OnBtnChange()
            },
            pw(wal){
                this.OnBtnChange()
            },
            phoneNum(val){
                this.OnBtnChange()
            },
            vericode(val){
                this.OnBtnChange()
            }
        }
	}
